/* Master Styles */
h1 {
 color: #369;
 font-family: Arial, Helvetica, sans-serif;
 font-size: 250%;
}
h2, h3 {
  color: #444;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: lighter;
}
body {
  margin: 2em;
}
body, input[text], button {
  font-family: Cambria, Georgia;
}
a {
  cursor: pointer;
  cursor: hand;
}

/* Navigation link styles */
nav a {
  padding: 5px 10px;
  text-decoration: none;
  margin: 10px 10px 10px 0px;
  display: inline-block;
  background-color: #eee;
  border-radius: 4px;
}
nav a:visited, a:link {
  color: #607D8B;
}
nav a:hover {
  color: #039be5;
  background-color: #CFD8DC;
}
nav a.active {
  color: #039be5;
}

/* everywhere else */
* {
  font-family: Arial, Helvetica, sans-serif;
}

.notes {
  margin: 0 0 2em 0;
  list-style-type: none;
  padding: 0;
  width: 15em;
}
.notes li {
  position: relative;
  cursor: pointer;
  background-color: #EEE;
  margin: .5em;
  padding: .3em 0;
  height: 1.6em;
  border-radius: 4px;
  overflow:hidden;
}
.notes li:hover {
  color: #607D8B;
  background-color: #DDD;
}
.notes li.selected {
  background-color: #BBD8DC;
}
.notes a {
  color: #888;
  text-decoration: none;
  position: relative;
  display: block;
  width: 250px;
}
.notes a:hover {
 color:#607D8B;
}
.notes .id {
  display: inline-block;
  font-size: small;
  color: white;
  padding: 0.8em 0.7em 0 0.7em;
  background-color: #607D8B;
  line-height: 1em;
  position: relative;
  left: -1px;
  top: -4px;
  height: 1.8em;
  min-width: 16px;
  text-align: right;
  margin-right: .8em;
  border-radius: 4px 0 0 4px;
}

.notes li > .inner {
  cursor: pointer;
  background-color: #EEE;
  height: 1.6em;
  border-radius: 4px;
  width: 19em;
}
.notes li:hover > .inner {
  color: #607D8B;
  background-color: #DDD;
  transform: translateX(.1em);
}

button {
  background-color: #eee;
  border: none;
  padding: 5px 10px;
  border-radius: 4px;
  cursor: pointer;
  cursor: hand;
  font-family: Arial;
}
button:hover {
  background-color: #cfd8dc;
}
button.delete {
  position: relative;
  left: 194px;
  top: -32px;
  background-color: gray !important;
  color: white;
}
button:disabled {
  background-color: #eee;
  color: #aaa;
  cursor: auto;
}

.w_100pc {
  position: relative;
  float: left;
  width: 100%;
}
.w_32pc{
  position: relative;
  float: left;
  width: 32%;
}
.w_24pc{
  position: relative;
  float: left;
  width: 24%;
}
